.page {
    .sidebar {
        width: 250px;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background: #292c33;
        color: #fff;
        padding: 15px;
        transition: .3s;

        &.no-sidebar {
            left: -250px;
        }
    }
}